body, body * {
  vertical-align: top;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.container {
  width: 100%;
  background-color: azure;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.container > * {
  z-index: 10;
}

.wrapper {
  opacity: .4;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

.title {
  text-align: center;
  width: 400px;
  background-color: #abe3d0;
  border-radius: 12px;
  margin-top: 4rem;
  padding: 16px;
}

.title span {
  font-style: italic;
  display: block;
}

.game-board {
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
  margin-bottom: 3rem;
  display: flex;
}

.grid {
  height: 404px;
  width: 404px;
  background-color: gray;
  border: 2px solid #2f4f4f;
  border-radius: 5px;
  flex-wrap: wrap;
  display: flex;
}

.sq {
  height: 40px;
  width: 40px;
  background-color: #cbd2d8;
  background-image: url("closed-sq.aab13aab.svg");
  background-size: cover;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 600;
  display: flex;
}

.checked {
  background-color: red;
  background-image: url("opened-sq.e961fa03.svg");
}

.restart-btn {
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  font-size: 2.25rem;
  display: flex;
}

.info {
  margin: .8rem 0;
  font-size: 1.25rem;
  font-weight: 600;
}

